// === Global >>>
*,
*::after,
*::before {
    box-sizing: border-box;
}

.container {
    max-width: 1320px;
    margin: 0 auto;
}

a {
    text-decoration: none; 
    color: $color-link;
    &:hover {
        transition: $transition;
        color: $link-hover-color;
    }
}

