.userPage {
    &-outer {
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 2rem;

        background-color: $mainPage-bg-color;
    }

    &-inner {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        position: relative;

        color: $color-secondary;
        background-color: $list-bg-color;
        font-size: 3rem;

        div + div {
            margin-bottom: 2rem;
        }
    }

    &-back {
        position: absolute;
        left: 2rem;
        cursor: pointer;

        &:hover {
            opacity: .8;
        }
    }

    &-imgWrap {
        display: flex;
        &__img {
            width: 15rem;
            height: 15rem;
            border-radius: 50%;
        }
    }

    &-name {
        &__link {
            color: $color-link;
        }
    }

    &-repos {
        .repos {
            &-linksWrap {
                display: flex;
                flex-direction: column;
                &-link {
                    display: block;
                }
            }
        }
    }

    &-organizations {
        text-align: center;
        &-ul {
            display: flex;
            flex-wrap: wrap;
            flex-basis: 30rem;
            flex-grow: 1;
            &__item {
                margin: 2rem;
                display: flex;
                flex-direction: column;
                &-img {
                    width: 8rem;
                    height: 8rem;
                }
                &-link {
                    font-size: $fs-h1;
                    color: $color-link;
                }
            }
        }
    }
}