
.mainPage {
    &-outer {
        width: 100%;
        height: auto;
        padding-bottom: 2rem;

        font-family: 'Montserrat', sans-serif;
        background-color: $mainPage-bg-color;
    }

    .header {
        &-outer {
            width: 100%;
            height: auto;
        }

        &-inner {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: $base*6 0;
        }

        &-title {
            flex-shrink: 0;
            color: $color-secondary;
            font-size: $fs-h1;
        }

        &-search {
            display: flex;
            flex-direction: column;
            margin: 0 $base*3;
            max-width: 500px;
            width: 100%;
            position: relative;

            &__input {
                display: inline-block;
                width: 100%;
                color: $color-primary;
                font-size: $fs-xl;
                padding: $base * 2;
                border: none;
                border-radius: $base;

                &:focus {
                    outline: none;
                    border: none;
                    box-shadow: 0 0 $base*1.5 $color-secondary;
                }
            }     

            &__history {
                display: none;
                flex-direction: column;
                width: 100%;
                height: auto;
                padding: $base * 2;
                border-radius: $base;
                background-color: $color-secondary;
                font-size: $fs-xl;
                margin-top: $base*12;
                position: absolute;

                &-item {
                    
                    color: $color-link;
                }
            }
        }

        &-viewType {
            display: flex;
            color: $color-secondary;
            font-size: $fs-xl;
            user-select: none;
            cursor: pointer;
            margin-right: $base*2;

            .text {
                margin-right: $base;
            }

            &:hover {
                transition: $transition;
                opacity: .6;
            }
        }
    }

    .loading {
        min-height: 100vh;
        text-align: center;
        font-size: $fs-h1;
        color: $color-secondary;
    }

    .list.users-wrapper {
        .users-wrapper__item {
            height: auto;
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: $list-bg-color;
            border: 1px solid $color-gray;
            border-radius: $base;
            overflow: hidden;
            padding: $base * 2;

            &:not(:last-child) {margin-bottom: $base*2;}

            &-info {
                display: flex;
                align-items: center;
                width: 100%;
                color: $color-primary;
                font-size: $fs-xl;

                &-img {
                    width: $base * 20;
                    height: $base * 20;
                    border-radius: 100%;
                }

                &-link {
                    margin-left: $base * 2;
                    font-size: $fs-xl;
                }

                &-type {
                    margin-left: $base * 2;
                }
            }

            .repos {
                flex-basis: 40rem;
                margin-right: $base * 5;
                font-size: $fs-xl;
                color: $color-secondary;
                display: flex;
                align-items: center;

                .text {
                    margin-right: $base * 2;
                }

                &-linksWrap {
                    display: flex;
                    flex-direction: column;
                    &-link {
                        display: block;
                    }
                }   
            }
        }
    }
    
 
    .grid.users-wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;

        .users-wrapper__item {
            flex: 1 0 300px;
            height: 300px;
            margin: $base * 2;
            background-color: $list-bg-color;
            border: 1px solid $color-gray;

            &-info {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                color: $color-primary;
                flex-wrap: wrap;
                font-size: $fs-xl;

                &-img {
                    width: $base * 20;
                    height: $base * 20;
                    border-radius: 100%;
                    margin: $base*2 0;
                }

                &-link {
                    line-height: 1.5;
                    color: $color-link;
                    margin-left: $base * 2;
                    font-size: $fs-xl;
                    
                    &:hover {
                        transition: $transition;
                        color: $link-hover-color;
                    }
                }

                &-type {
                    margin-left: $base * 2;
                }
            }

            .repos {
                height: auto;
                width: 100%;

                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                margin-top: $base * 2;
                
                font-size: $fs-xl;
                text-align: center;

                .text {
                    margin-right: $base * 2;
                    margin-bottom: $base * 2;
                }

                &-linksWrap {
                    &-link {
                        display: block;
                        color: $color-link;

                        &:hover {
                            transition: $transition;
                            color: $link-hover-color;
                        }
                    }
                }   
            }
        }
    }
}
